"use client";

import React, { useState, useEffect } from "react";
import { generateQuotePDF } from '@/lib/pdfGenerator';
import { useRouter, useParams } from "next/navigation";
import Header from "@/components/Header";
import { Product } from "@/lib/database";
import Link from "next/link";

export default function ProjectDetailPage() {
  const params = useParams();
  const projectId = Number(params?.id);
  const [project, setProject] = useState<any>(null);
  const [editProjectName, setEditProjectName] = useState<string>("");
  const [projectProducts, setProjectProducts] = useState<any[]>([]);
  // Inline edit state (her hücre için ayrı)
  const [editingCell, setEditingCell] = useState<{row: number, field: string} | null>(null);
  const [editCellValue, setEditCellValue] = useState<string | number>('');
  const [addProductId, setAddProductId] = useState<number>(0);
  const [addQuantity, setAddQuantity] = useState<number>(1);
  const [categoryFilter, setCategoryFilter] = useState<string>('');
  const [productSearch, setProductSearch] = useState<string>('');
  const [allProducts, setAllProducts] = useState<Product[]>([]);
  const [isLoadingProducts, setIsLoadingProducts] = useState(false);
  const router = useRouter();
  const [isSaving, setIsSaving] = useState(false);
  // Projeyi kaydet (her zaman güncel ürünlerle kaydeder)
  async function handleSaveProject() {
    setIsSaving(true);
    try {
      const res = await fetch(`/api/projects/${projectId}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: editProjectName || `Proje ${projectId}`,
          products: projectProducts.map(p => ({
            productId: p.id ?? p.productId, // Hem yeni eklenen hem eski formatı destekle
            quantity: p.quantity,
            discount: p.discount ?? 0,
            margin: p.margin ?? 0,
          }))
        })
      });
      if (!res.ok) throw new Error('Kayıt başarısız');
      router.push('/projeler');
    } catch (e) {
      alert('Proje kaydedilemedi!');
    } finally {
      setIsSaving(false);
    }
  }
  // Teklif PDF için müşteri modalı
  const [showCustomerModal, setShowCustomerModal] = useState(false);
  const [customerList, setCustomerList] = useState<any[]>([]);
  const [selectedCustomerId, setSelectedCustomerId] = useState<number | null>(null);
  const [customerName, setCustomerName] = useState('');
  const [customerCompany, setCustomerCompany] = useState('');
  const [customerEmail, setCustomerEmail] = useState('');
  const [customerPhone, setCustomerPhone] = useState('');
  // Teklif geçerlilik ve teslim süresi
  const [validityPeriod, setValidityPeriod] = useState('15');
  const [deliveryTime, setDeliveryTime] = useState('2 hafta');

  // Müşteri listesini çek
  useEffect(() => {
    if (showCustomerModal) {
      fetch('/api/customers')
        .then(res => res.json())
        .then(data => setCustomerList(data));
    }
  }, [showCustomerModal]);

  // Proje ve ürünleri yükle
  useEffect(() => {
    async function fetchProjectAndProducts() {
      try {
        // Hem projeyi hem ürünleri paralel çek
        const [projectRes, productsRes] = await Promise.all([
          fetch(`/api/projects/${projectId}`),
          fetch('/api/products')
        ]);
        if (!projectRes.ok) throw new Error('Proje bulunamadı');
        const projectData = await projectRes.json();
        const allProductsData = productsRes.ok ? await productsRes.json() : [];
  setProject({ id: projectData.id, name: projectData.name });
  setEditProjectName(projectData.name || "");
        // Proje ürünlerini detaylarla birleştir
        const mergedProducts = Array.isArray(projectData.products)
          ? projectData.products.map((p: any) => {
              const prod = allProductsData.find((ap: any) => ap.id === (p.productId ?? p.id));
              return {
                ...prod,
                ...p,
                id: p.productId ?? p.id,
              };
            })
          : [];
        setProjectProducts(mergedProducts);
      } catch {
        setProject({ id: projectId, name: `Proje ${projectId}` });
        setProjectProducts([]);
      }
    }
    fetchProjectAndProducts();
  }, [projectId]);

  // Ürünleri API'den çek
  useEffect(() => {
    async function fetchProducts() {
      setIsLoadingProducts(true);
      try {
        const res = await fetch('/api/products');
        const data = await res.json();
        setAllProducts(data);
      } catch (err) {
        setAllProducts([]);
      } finally {
        setIsLoadingProducts(false);
      }
    }
    fetchProducts();
  }, []);

  // Ürün ekle
  function addProductToProject() {
    if (!addProductId) return;
    const prod = allProducts.find((p) => p.id === addProductId);
    if (!prod) return;
    setProjectProducts((prev) => [
      ...prev,
      {
        ...prod,
        quantity: addQuantity,
        discount: 0,
        margin: 0,
      },
    ]);
    setAddProductId(0);
    setAddQuantity(1);
    setProductSearch('');
  }

  // Inline edit handlers (her hücre için)
  function startEditCell(row: number, field: string, value: number) {
    setEditingCell({ row, field });
    setEditCellValue(value);
  }
  function saveEditCell(row: number, field: string) {
    setProjectProducts(prev => prev.map((item, i) => i === row ? {
      ...item,
      [field]: Number(editCellValue)
    } : item));
    setEditingCell(null);
    setEditCellValue('');
  }

  // Hesaplamalar
  const totalCost = projectProducts.reduce(
    (sum, p) => sum + (p.price * p.quantity * (1 - p.discount / 100)),
    0
  );
  const totalSales = projectProducts.reduce(
    (sum, p) =>
      sum + (p.price * p.quantity * (1 - p.discount / 100) * (1 + p.margin / 100)),
    0
  );
  const totalProfit = totalSales - totalCost;

  // Proje ürününden silme fonksiyonu
  function handleDeleteProjectProduct(index: number) {
    setProjectProducts(prev => prev.filter((_, i) => i !== index));
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-yellow-50 via-orange-50 to-white py-10 px-2 md:px-8">
      <Header />
      <div className="max-w-4xl mx-auto flex flex-col gap-8">
        <div className="bg-white/95 rounded-3xl shadow-premium p-8 border border-white/20">
          <div className="flex items-center justify-between mb-6">
            <input
              type="text"
              className="text-2xl font-black text-gray-900 bg-white border-b-2 border-yellow-300 focus:border-orange-500 outline-none px-2 py-1 rounded transition-all min-w-[180px]"
              value={editProjectName}
              onChange={e => setEditProjectName(e.target.value)}
              placeholder="Proje Adı"
              style={{maxWidth:'70%'}}
            />
            <Link href="/projeler" className="text-sm text-orange-600 font-bold">← Projelere Dön</Link>
          </div>

          {/* Ürün ekleme */}
          <div className="mb-6 flex flex-col md:flex-row gap-2 md:gap-4 items-end relative">
            {/* Kategori seçimi */}
            <select
              className="w-48 border px-3 py-2 rounded-lg text-sm text-gray-900 bg-white"
              value={categoryFilter}
              onChange={e => setCategoryFilter(e.target.value)}
            >
              <option value="">Tüm Kategoriler</option>
              {[...new Set(allProducts.map(p => p.category).filter(Boolean))].map(cat => (
                <option key={cat} value={cat}>{cat}</option>
              ))}
            </select>
            {/* Ürün arama inputu */}
            <input
              type="text"
              value={productSearch}
              onChange={e => setProductSearch(e.target.value)}
              className="w-64 border px-3 py-2 rounded-lg text-sm text-gray-900"
              placeholder="Ürün ara..."
            />
            {/* Autocomplete dropdown sadece arama varsa */}
            {productSearch && (
              <div className="absolute left-0 right-0 top-full bg-white border border-gray-200 rounded-lg shadow z-20 max-h-60 overflow-y-auto">
                {allProducts
                  .filter(p =>
                    (!categoryFilter || p.category === categoryFilter) &&
                    p.name.toLowerCase().includes(productSearch.toLowerCase())
                  )
                  .slice(0, 15)
                  .map(p => (
                    <div
                      key={p.id}
                      className={`px-4 py-2 cursor-pointer hover:bg-yellow-100 ${addProductId === p.id ? 'bg-yellow-50' : ''}`}
                      onClick={() => {
                        setAddProductId(p.id!);
                        setProductSearch(p.name);
                      }}
                    >
                      <span className="block text-gray-900 font-bold text-base leading-tight">{p.name}</span>
                      <span className="block text-xs text-gray-500 font-mono">{p.code}</span>
                    </div>
                  ))}
                {/* Hiç ürün yoksa */}
                {allProducts.filter(p => (!categoryFilter || p.category === categoryFilter) && p.name.toLowerCase().includes(productSearch.toLowerCase())).length === 0 && (
                  <div className="px-4 py-2 text-gray-400">Ürün bulunamadı</div>
                )}
              </div>
            )}
            {/* Adet inputu */}
            <input
              type="number"
              min={1}
              value={addQuantity}
              onChange={e => setAddQuantity(Number(e.target.value))}
              className="w-20 border px-3 py-2 rounded-lg text-sm text-gray-900"
              placeholder="Adet"
            />
            <button
              onClick={addProductToProject}
              className="bg-gradient-to-r from-yellow-500 to-orange-500 hover:from-yellow-600 hover:to-orange-600 text-white px-4 py-2 rounded-lg font-bold text-sm"
              disabled={!addProductId || addQuantity < 1}
            >
              Ekle
            </button>
          </div>

          {/* Ürün listesi */}
          <div className="overflow-x-auto">
            <table className="min-w-full text-sm border rounded-xl overflow-hidden">
              <thead>
                <tr className="bg-yellow-100 text-gray-800">
                  <th className="px-4 py-2">Ürün</th>
                  <th className="px-4 py-2">Kod</th>
                  <th className="px-4 py-2">Adet</th>
                  <th className="px-4 py-2">Liste Fiyatı</th>
                  <th className="px-4 py-2">İskonto (%)</th>
                  <th className="px-4 py-2">Kar Marjı (%)</th>
                  <th className="px-4 py-2">Satış Fiyatı</th>
                  <th className="px-4 py-2">Toplam</th>
                </tr>
              </thead>
              <tbody>
                {projectProducts.map((p, i) => (
                  <tr key={i} className="border-b">
                    <td className="px-4 py-2 font-bold text-gray-900">{p.name}</td>
                    <td className="px-4 py-2 text-gray-700">{p.code}</td>
                    {/* Adet */}
                    <td className="px-4 py-2 text-center">
                      {editingCell?.row === i && editingCell.field === 'quantity' ? (
                        <input
                          type="number"
                          min={1}
                          value={editCellValue}
                          onChange={e => setEditCellValue(e.target.value)}
                          onBlur={() => saveEditCell(i, 'quantity')}
                          onKeyDown={e => { if (e.key === 'Enter') saveEditCell(i, 'quantity'); }}
                          className="w-16 border px-2 py-1 rounded text-gray-900 font-bold text-center"
                          autoFocus
                        />
                      ) : (
                        <span className="font-bold text-gray-900 cursor-pointer" onClick={() => startEditCell(i, 'quantity', p.quantity)}>{p.quantity}</span>
                      )}
                    </td>
                    {/* Fiyat */}
                    <td className="px-4 py-2 text-right">
                      {editingCell?.row === i && editingCell.field === 'price' ? (
                        <input
                          type="number"
                          min={0}
                          value={editCellValue}
                          onChange={e => setEditCellValue(e.target.value)}
                          onBlur={() => saveEditCell(i, 'price')}
                          onKeyDown={e => { if (e.key === 'Enter') saveEditCell(i, 'price'); }}
                          className="w-24 border px-2 py-1 rounded text-green-800 font-bold text-right"
                          autoFocus
                        />
                      ) : (
                        <span className="font-bold text-green-800 cursor-pointer" onClick={() => startEditCell(i, 'price', p.price)}>
                          €{p.price?.toLocaleString("tr-TR", { minimumFractionDigits: 2 })}
                        </span>
                      )}
                    </td>
                    {/* İskonto */}
                    <td className="px-4 py-2 text-center">
                      {editingCell?.row === i && editingCell.field === 'discount' ? (
                        <input
                          type="number"
                          min={0}
                          max={100}
                          value={editCellValue}
                          onChange={e => setEditCellValue(e.target.value)}
                          onBlur={() => saveEditCell(i, 'discount')}
                          onKeyDown={e => { if (e.key === 'Enter') saveEditCell(i, 'discount'); }}
                          className="w-14 border px-2 py-1 rounded text-blue-800 font-bold text-center"
                          autoFocus
                        />
                      ) : (
                        <span className="font-bold text-blue-800 cursor-pointer" onClick={() => startEditCell(i, 'discount', p.discount ?? 0)}>{p.discount ?? 0}</span>
                      )}
                    </td>
                    {/* Kar Marjı */}
                    <td className="px-4 py-2 text-center">
                      {editingCell?.row === i && editingCell.field === 'margin' ? (
                        <input
                          type="number"
                          min={0}
                          max={100}
                          value={editCellValue}
                          onChange={e => setEditCellValue(e.target.value)}
                          onBlur={() => saveEditCell(i, 'margin')}
                          onKeyDown={e => { if (e.key === 'Enter') saveEditCell(i, 'margin'); }}
                          className="w-14 border px-2 py-1 rounded text-orange-800 font-bold text-center"
                          autoFocus
                        />
                      ) : (
                        <span className="font-bold text-orange-800 cursor-pointer" onClick={() => startEditCell(i, 'margin', p.margin ?? 0)}>{p.margin ?? 0}</span>
                      )}
                    </td>
                    {/* Satış Fiyatı */}
                    <td className="px-4 py-2 text-right font-bold text-green-700">
                      €{(p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100)).toLocaleString("tr-TR", { minimumFractionDigits: 2 })}
                    </td>
                    {/* Toplam */}
                    <td className="px-4 py-2 text-right font-bold text-blue-700">
                      €{(p.price * p.quantity * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100)).toLocaleString("tr-TR", { minimumFractionDigits: 2 })}
                    </td>
                    {/* Sil butonu */}
                    <td className="px-2 py-2 text-center">
                      <button
                        onClick={() => handleDeleteProjectProduct(i)}
                        className="bg-red-50 hover:bg-red-100 text-red-700 font-bold py-1 px-2 rounded text-xs border border-red-200 transition-all"
                        title="Ürünü projeden çıkar"
                      >Sil</button>
                    </td>
                  </tr>
                ))}
                {projectProducts.length === 0 && (
                  <tr>
                    <td colSpan={8} className="text-center text-gray-400 py-8">Henüz ürün eklenmedi.</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>


          {/* Ara Toplam, KDV ve Genel Toplam */}
          <div className="flex flex-col items-end gap-1 mt-6 text-base font-bold">
            {/* Ara Toplam (satış fiyatı üzerinden) */}
            <div className="text-blue-700">
              Ara Toplam: €{
                projectProducts.reduce((sum, p) => sum + (p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100) * p.quantity), 0)
                .toLocaleString("tr-TR", { minimumFractionDigits: 2 })
              }
            </div>
            {/* KDV */}
            <div className="text-orange-700">
              KDV (%20): €{
                (projectProducts.reduce((sum, p) => sum + (p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100) * p.quantity), 0) * 0.2)
                .toLocaleString("tr-TR", { minimumFractionDigits: 2 })
              }
            </div>
            {/* Genel Toplam */}
            <div className="text-green-700">
              Genel Toplam: €{
                (projectProducts.reduce((sum, p) => sum + (p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100) * p.quantity), 0) * 1.2)
                .toLocaleString("tr-TR", { minimumFractionDigits: 2 })
              }
            </div>
          </div>

          {/* Projeyi Kaydet ve Teklif oluştur butonları */}
          <div className="flex flex-col md:flex-row justify-end gap-4 mt-8">
            <button
              className="bg-gradient-to-r from-green-500 to-blue-500 hover:from-green-600 hover:to-blue-600 text-white px-8 py-3 rounded-xl font-black text-lg shadow-lg disabled:opacity-60 disabled:cursor-not-allowed"
              onClick={handleSaveProject}
              disabled={isSaving || projectProducts.length === 0}
            >
              {isSaving ? 'Kaydediliyor...' : 'Projeyi Kaydet'}
            </button>
            <button
              className="bg-gradient-to-r from-yellow-500 to-orange-500 hover:from-yellow-600 hover:to-orange-600 text-white px-8 py-3 rounded-xl font-black text-lg shadow-lg"
              onClick={() => setShowCustomerModal(true)}
            >
              Teklif Oluştur (PDF)
            </button>
          </div>
          {/* Müşteri Bilgisi Modalı */}
          {showCustomerModal && (
            <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/30">
              <div className="bg-white rounded-2xl shadow-xl p-8 w-full max-w-md">
                <h3 className="text-xl font-bold mb-4 text-blue-900">Müşteri Seçimi</h3>
                <div className="mb-4">
                  <label className="block text-xs font-bold text-gray-700 mb-1">Kayıtlı Müşteriler</label>
                  <select
                    className="w-full border px-3 py-2 rounded-lg text-sm text-gray-900 font-semibold bg-white"
                    value={selectedCustomerId ?? ''}
                    onChange={e => {
                      const id = Number(e.target.value);
                      setSelectedCustomerId(id);
                      const cust = customerList.find(c => c.id === id);
                      setCustomerName(cust?.name || '');
                      setCustomerCompany(cust?.company || '');
                      setCustomerEmail(cust?.email || '');
                      setCustomerPhone(cust?.phone || '');
                    }}
                  >
                    <option value="" className="text-gray-500 font-normal">Müşteri Seçin</option>
                    {customerList.map(c => (
                      <option key={c.id} value={c.id} className="text-gray-900 font-semibold">{c.name} {c.company ? `(${c.company})` : ''}</option>
                    ))}
                  </select>
                </div>
                {/* Seçili müşteri bilgileri ve teklif detayları */}
                {selectedCustomerId && (
                  <>
                  <div className="space-y-2 mb-2 text-sm bg-white rounded-lg p-3 border border-gray-200 shadow-sm">
                    <div className="text-gray-900"><b>Ad Soyad:</b> {customerName}</div>
                    {customerCompany && <div className="text-gray-900"><b>Firma:</b> {customerCompany}</div>}
                    {customerEmail && <div className="text-gray-900"><b>E-posta:</b> {customerEmail}</div>}
                    {customerPhone && <div className="text-gray-900"><b>Telefon:</b> {customerPhone}</div>}
                  </div>
                  <div className="flex gap-2 mb-2">
                    <div className="flex-1">
                      <label className="block text-xs font-bold text-gray-700 mb-1">Teklif Geçerlilik Süresi (gün)</label>
                      <input type="text" className="w-full border px-3 py-2 rounded-lg text-sm text-gray-900" value={validityPeriod} onChange={e=>setValidityPeriod(e.target.value)} placeholder="Örn: 15" />
                    </div>
                    <div className="flex-1">
                      <label className="block text-xs font-bold text-gray-700 mb-1">Teslim Süresi</label>
                      <input type="text" className="w-full border px-3 py-2 rounded-lg text-sm text-gray-900" value={deliveryTime} onChange={e=>setDeliveryTime(e.target.value)} placeholder="Örn: 2 hafta" />
                    </div>
                  </div>
                  </>
                )}
                <div className="flex justify-end gap-2 mt-6">
                  <button className="px-4 py-2 rounded-lg bg-gray-200 text-gray-700 font-bold" onClick={()=>setShowCustomerModal(false)}>Vazgeç</button>
                  <button
                    className="px-4 py-2 rounded-lg bg-blue-600 text-white font-bold disabled:opacity-60"
                    disabled={!selectedCustomerId}
                    onClick={async()=>{
                      setShowCustomerModal(false);
                      const cust = customerList.find(c => c.id === selectedCustomerId);
                      await generateQuotePDF({
                        quoteId: projectId,
                        customerInfo: {
                          name: cust?.name || '',
                          company: cust?.company || '',
                          email: cust?.email || '',
                          phone: cust?.phone || '',
                        },
                        projectDetails: {
                          projectDesign: project?.name || `Proje ${projectId}`,
                          projectDescription: '',
                        },
                        schedule: {
                          startDate: '',
                          endDate: '',
                        },
                        conditions: {
                          validityPeriod: validityPeriod,
                          deliveryTime: deliveryTime,
                        },
                        items: projectProducts.map(p => {
                          const salesUnit = p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100);
                          return {
                            product: {
                              name: p.name,
                              brand: p.brand,
                              model: p.model,
                              code: p.code,
                            },
                            quantity: p.quantity,
                            unitPrice: salesUnit,
                            total: salesUnit * p.quantity,
                          };
                        }),
                        subtotal: projectProducts.reduce((sum, p) => sum + (p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100) * p.quantity), 0),
                        kdv: 0,
                        total: projectProducts.reduce((sum, p) => sum + (p.price * (1 - (p.discount ?? 0) / 100) * (1 + (p.margin ?? 0) / 100) * p.quantity), 0),
                        createdAt: new Date().toISOString(),
                      });
                    }}
                  >PDF Oluştur</button>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
